.detail {
    font-family: 'Segoe UI';
    font-style: normal;
    background-color: #0B1219;
    height: 100vh;
    overflow-x: hidden;

    .greenline {
        width: 100%;
        background: #06554D;
        height: 100px;
        position: absolute;
        margin-top: 134px;
    }

    .contan {
        max-width: 1200px;
        margin: 0 auto;
        margin-top: 74px;

        .chunk {
            display: flex;
            justify-content: space-between;

            .chunkleftdiv {
                width: 42%;
                margin: auto auto;
                border: 1px solid #2C3034;
                z-index: 1;

                .chunkleft {
                    margin-left: auto;
                    margin-right: auto;
                    margin: 0.625rem;

                    img {
                        width: 100%;
                        height: 100%;
                    }
                }
            }

            .chunkright {
                width: 55%;
                color: #FFFFFFFF;
                font-size: 22px;
                z-index: 1;
                margin-left: 7%;
                margin-top: 98px;

                .fullName {
                    font-weight: 700;
                    margin-bottom: 70px;
                }

                .fullInfo {
                    font-weight: 400;
                }
            }
        }
    }
}

@media (max-width:1025px) {
    #chunkleftdiv {
        min-width: 350px;
        font-size: 20px;
    }

    #chunkright {
        font-size: 20px;
    }
}

@media (max-width:962px) {
    .chunkleftdiv {
        min-width: 360px;
    }

    #fullName {
        font-size: 19px;
    }

    #fullInfo {
        font-size: 19px;
    }
}

@media (max-width:860px) {
    #chunkleftdiv {
        margin-top: 20px;
        min-width: 360px;
    }

    #fullName {
        font-size: 18px;
    }

    #fullInfo {
        font-size: 16px;
    }
}

@media (max-width:809px) {
    #chunkleftdiv {
        margin-top: 20px;
        min-width: 360px;
    }

    #chunkrifht {
        width: 60%;
    }

